import React from 'react'
import css from './footer.css'

class Footer extends React.Component {
    render () {
      return <div className={css.footer}>
        <div>
          logo
        </div>
        <div>
          location
          <img src="data:image/png;base64,"/>
          <a>email</a>
        </div>
        <div>
          rights reserved
        </div>
      </div>
    }
  }

  export default Footer